@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&family=Space+Grotesk:wght@400;500&display=swap);
html {
  margin: 0;
  padding: 0;
  -webkit-transform: translate3d(0, 0, 0);
}

body {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

a {
  text-decoration: none;
  color: black;
}

input {
  border: 0.5px solid rgb(80, 76, 143, 0.5);
  border-radius: 15px;
  font-size: 40px;
  padding: 0 0 0 5px;
  outline-color: rgb(103, 103, 161);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.bgImage {
  position: absolute;
  z-index: -5;
  padding: 0;
  top: 100vh;
  height: 100%;
  width: 100%;
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 38px;
}

button {
  border: none;
  background-color: rgba(192, 192, 192, 0.3);
  -webkit-backdrop-filter: blur(55px);
          backdrop-filter: blur(55px);
  color: black;
  border: 1px solid white;
  padding: 10px;
  font-size: 16px;
  font-weight: 400;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.3s ease;
}

button:hover {
  transform: translateY(-2px);
  color: black;
}

.errorMsg {
  animation: 1s error infinite ease;
  color: red;
}

@keyframes error {
  100%, 0% {
    opacity: 0
  }
  50% {
    opacity: 1
  }
}

.boldText {
  font-weight: 400;
}

.visible {
  opacity: 1;
}

.invisible {
  opacity: 0;
  transition: 1s ease-in-out;
}

.pricesArrow:hover,
.bitcoinArrow:hover {
  cursor: pointer;
}

.homepageArrow:hover {
  cursor: pointer;
}

.skeletonInflation {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  grid-gap: 1em;
  gap: 1em;
  padding: 2em;
}

.skeletonCrypto {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  grid-gap: 1em;
  gap: 1em;
  padding: 2em;
  grid-gap: 3em;
  gap: 3em;
}

.skeleton-icon {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-color: rgb(206, 206, 206);
  animation: skeleton 2s infinite ease;
}

.skeletonTextArea {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  grid-gap: 15px;
  gap: 15px;
}

.skeletonTextArea .skeleton-text {
  height: 10px;
  width: 60%;
  background-color: rgb(231, 231, 231);
  animation: skeleton 2s infinite ease;
}

.skeletonTextArea .skeleton-text-big {
  height: 20px;
  width: 90%;
  background-color: rgb(231, 231, 231);
  animation: skeleton 2s infinite ease;
}

.skeleton-text {
  width: 50%;
  height: 18px;
  background-color: rgb(231, 231, 231);
  animation: skeleton 2s infinite ease;
}

.skeleton-text-big {
  width: 90%;
  height: 20px;
  background-color: rgb(231, 231, 231);
  animation: skeleton 2s infinite ease;
}

@keyframes skeleton {
  100%,
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes float {
  0% {
    opacity: 0;
    transform: scale(1);
  }

  50% {
    opacity: 1;
    transform: scale(1.1);
  }

  100% {
    opacity: 0;
    transform: scale(1.3);
  }
}

.routePage {
  filter: blur(0px);
  transition: filter 1s cubic-bezier(0.4, 0.01, 0.23, 1.01);
  overflow: hidden;
}

.routePageUnclickable {
  pointer-events: none;
  filter: blur(4px);
}

/* media queries */

@media screen and (min-width: 768px) {
  h1 {
    font-size: 65px;
  }

  h2 {
    font-size: 60px;
  }

  input {
    font-size: 65px;
  }

  button {
    font-size: 25px;
    padding: 1em;
  }

  .routePage {
    transition: filter 1s cubic-bezier(0.4, 0.01, 0.23, 1.01);
  }

  .routePageUnclickable {
    filter: blur(16px);
  }
}

@media screen and (min-width: 1024px) {
  .skeletonCrypto {
    border-radius: 15px;
    border: 1px solid rgb(221, 221, 221);
  }

  .skeletonInflation {
    border-radius: 15px;
    border: 1px solid rgb(221, 221, 221);
  }

  h1 {
    font-size: 50px;
  }

  input {
    font-size: 50px;
  }

  button {
    font-size: 20px;
  }

  .routePageUnclickable {
    filter: blur(0);
    pointer-events: auto;
  }
}

